.nav {
    width: 164px;
    height: 294px;
    display: flex;
    align-content: center;
    flex-direction: column;
    margin-top: 10px;
}

.item {
    font-size: 15px;
    font-weight: 600;
    color: #eeedf8;
    margin-left: 20px;
    margin-bottom: 10px;
    width: 100%;
    /*background-color: hsla(0,0%,100%,.2);*/
    /*border-top-left-radius: 20px;*/
    /*border-bottom-left-radius: 20px;*/
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
}

.active {
    /*background-color: hsla(0, 0%, 100%, .2);*/
    font-size: 15px;
    font-weight: 600;
    color: #eeedf8;
    margin-left: 20px;
    margin-bottom: 10px;
    width: 100%;
    background-color: hsla(0,0%,100%,.2);
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    cursor: pointer;
}

:root {
    --d: 2500ms;
    --angle: 90deg;
    --gradX: 100%;
    --gradY: 50%;
    --c1: rgba(168, 239, 255, 1);
    --c2: rgba(168, 239, 255, 0.1);
}

